.page {
  height: 100%;
}

.page-modal {
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  z-index: 50;
}

.page-modal__container {
  z-index: 100;
  display: block;
  box-sizing: border-box;
  margin: 50% auto;
  width: 100%;
  height: 336px;
  background: #ffffff;
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
  text-align: center;
}
@media (min-width: 768px) {
  .page-modal__container {
    margin: 20% auto;
    width: 566px;
    height: 336px;
  }
}

.page-modal__hide {
  display: none;
}

.page-modal__show {
  display: block;
}

.page-modal__title {
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
  text-transform: uppercase;
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .page-modal__title {
    font-size: 30px;
    line-height: 57px;
    margin-top: 37px;
  }
}

@media (min-width: 768px) {
  .page-modal__visually-hidden {
    display: none;
  }
}

.page-modal__subtitle {
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 30px;
  margin-top: 14px;
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .page-modal__subtitle {
    font-size: 17px;
    margin-top: 11px;
  }
}

.field-size {
  border: none;
}

.field-size__board {
  margin-top: 35px;
  margin-bottom: 35px;
}
@media (min-width: 768px) {
  .field-size__board {
    margin-top: 32px;
  }
}

.field-size__label {
  position: relative;
  margin-right: 50px;
