    font-size: 17px;
    line-height: 16px;
    margin-right: 78px;
    margin-left: 12px;
  }
}

.field-size__label:nth-last-child(1) {
  margin-right: 0;
}

.field-size__checkbox {
  font-weight: bold;
  font-size: 17px;
  line-height: 16px;
  position: absolute;
  cursor: pointer;
  content: "";
  width: 78px;
  height: 48px;
  left: -38px;
  top: -16px;
  border: 2px solid #444444;
  opacity: 0.1;
}
