    left: -52px;
    width: 107px;
    height: 48px;
  }
}

.field-size__checkbox:hover {
  opacity: 0.3;
}

.field-size__input {
  appearance: none;
}

.field-size__input:checked + .field-size__checkbox {
  border: 2px solid #62d1ba;
  opacity: 1;
}

.page-modal__button {
  cursor: pointer;
  font-weight: bold;
  font-size: 17px;
  line-height: 16px;
  text-transform: uppercase;
  background: #63d1bb;
  color: #ffffff;
