  .page-body {
    grid-template-columns: 1fr repeat(6, 175px) 1fr;
    grid-template-rows: min-content 1fr min-content;
  }
}

.page-body__visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

