@import "./variables.scss";

@mixin BtnVisited {
  color: $Tundora;
}

@mixin BtnHover {
  color: $White;
  background: linear-gradient(0deg, $Tundora, $Tundora);
  border: 2px solid $Tundora;
}

@mixin BtnActive {
  color: $whiteRgba;
  background: linear-gradient(0deg, $Tundora, $Tundora);
  border: 2px solid $Tundora;
}

@mixin VisuallyHidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
