@import "../abstracts/variables.scss";
@import "../abstracts/mixins.scss";

.header-block {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-style: normal;
  font-weight: bold;
  margin: 0;
  padding: 0;
  background: $Alabaster url("../img/hero-mobile@1x.jpg") no-repeat 50% -18px;
  box-shadow: 0 -1px 0 $Gallery, inset 0 -1px 0 $Gallery;

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    background: $Alabaster url("../img/hero-mobile@2x.jpg") no-repeat 50% -18px;
    background-size: 575px auto;
  }

  @media (min-width: $tablet-width) {
    margin-top: -71px;
    background: $Alabaster url("../img/hero-tablet@1x.jpg") no-repeat 50% 0;

    @media (min-width: $tablet-width) and (min-resolution: 144dpi),
      (min-resolution: 1.5dppx) {
      margin-top: -71px;
      background: $Alabaster url("../img/hero-tablet@2x.jpg") no-repeat 50% 0;
      background-size: 1150px auto;
    }
  }

  @media (min-width: $desktop-width) {
    margin-top: -72px;
    background: $Alabaster url("../img/hero-desktop@1x.jpg") no-repeat 50% 0;

    @media (min-width: $desktop-width) and (min-resolution: 144dpi),
      (min-resolution: 1.5dppx) {
      margin-top: -72px;
      background: $Alabaster url("../img/hero-desktop@2x.jpg") no-repeat 50% 0;
      background-size: 1150px auto;
    }
  }
}

.header-block::after {
  @media (min-width: $tablet-width) {
    position: absolute;
    content: "";
    background-image: url("../img/bg-zigzag-fill.svg");
    background-repeat: repeat-x;
    background-size: 768px;
    width: 768px;
    height: 17px;
    transform: translateX(-50%);
    left: 50%;
    top: 98%;
  }

  @media (min-width: $desktop-width) {
    background-size: 1050px;
    width: 1050px;
    height: 23px;
  }
}

.header-block__container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 260px;

  @media (min-width: $tablet-width) {
    width: 668px;
    margin-top: 0;
  }

  @media (min-width: $desktop-width) {
    width: 1050px;
  }
}

.header-block__title {
  text-align: center;
  font-size: 27px;
  line-height: 30px;
  margin-top: 72px;
  margin-bottom: 120px;
  width: 260px;
  height: 90px;

  @media (min-width: $tablet-width) {
    font-size: 43px;
    line-height: 50px;
    margin-top: 153px;
    margin-bottom: 372px;
    width: 668px;
    height: 100px;
  }

  @media (min-width: $desktop-width) {
    font-size: 53px;
    line-height: 57px;
    margin-top: 158px;
    margin-bottom: 355px;
    width: 700px;
    height: 114px;
  }
}

.header-block__list {
  align-self: stretch;
  font-size: 19px;
  line-height: 24px;
  color: $White;
  list-style: none;
  margin: 0;
  padding: 0;
  margin-left: -30px;
  margin-right: -30px;

  @media (min-width: $tablet-width) {
    margin: 0;
    display: flex;
    flex-direction: row;
    font-size: 20px;
    line-height: 30px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: row;
    font-size: 20px;
    line-height: 30px;
  }
}

.header-block__link,
.header-block__link:visited {
  color: $White;
}

.header-block__item-title {
  padding: 0;
  margin: 0;
}

.header-block__item {
  width: 100%;
}

.header-block__item:first-child .header-block__link {
  position: relative;
  display: block;
  padding: 24px 184px 31px 30px;
  background-color: $Downy-1;
  margin-top: 0;
  margin-bottom: 0;

  @media (min-width: $tablet-width) {
    padding: 25px 167px 34px 36px;
  }

  @media (min-width: $desktop-width) {
    padding: 25px 350px 34px 37px;
  }
}

.header-block__item:first-child .header-block__link:hover {
  background-color: $Viking;
  padding: 29px 184px 36px 30px;
  margin-top: -10px;

  @media (min-width: $tablet-width) {
    padding: 30px 167px 39px 36px;
  }

  @media (min-width: $desktop-width) {
    padding: 25px 350px 44px 37px;
  }
}

.header-block__item:first-child .header-block__link::before {
  content: "";
  position: absolute;
  top: 23px;
  left: 244px;
  width: 60px;
  height: 65px;
  background: url("../img/icon-interior.svg") no-repeat;
  opacity: 0.5;

  @media (min-width: $tablet-width) {
    width: 60px;
    height: 65px;
    top: 30px;
    left: 258px;
  }

  @media (min-width: $desktop-width) {
    top: 30px;
    left: 448px;
    width: 60px;
    height: 65px;
  }
}

.header-block__item:last-child .header-block__link {
  position: relative;
  display: block;
  padding: 24px 201px 31px 30px;
  background-color: $FountainBlue;
  margin-top: 0;
  margin-bottom: 0;

  @media (min-width: $tablet-width) {
    padding: 25px 167px 34px 36px;
  }

  @media (min-width: $desktop-width) {
    padding: 25px 350px 34px 37px;
  }
}

.header-block__item:last-child .header-block__link:hover {
  background-color: $PuertoRico;
  padding: 29px 201px 36px 30px;
  margin-top: -10px;

  @media (min-width: $tablet-width) {
    padding: 30px 167px 39px 36px;
  }

  @media (min-width: $desktop-width) {
    padding: 25px 350px 44px 37px;
  }
}

.header-block__item:last-child .header-block__link::before {
  content: "";
  position: absolute;
  top: 21px;
  left: 233px;
  width: 60px;
  height: 60px;
  background: url("../img/icon-toy.svg") no-repeat;
  opacity: 0.5;

  @media (min-width: $tablet-width) {
    top: 30px;
    left: 243px;
    width: 61px;
    height: 61px;
  }

  @media (min-width: $desktop-width) {
    top: 30px;
    left: 430px;
    width: 61px;
    height: 61px;
  }
}

.header-block__visually-hidden {
  @media (min-width: $tablet-width) {
    display: none;
  }

  @media (min-width: $desktop-width) {
    display: none;
  }
}
