@import "../abstracts/variables.scss";
@import "../abstracts/mixins.scss";

.page-header {
  grid-column: 2/-2;
  grid-row: 1/2;
  position: relative;
  margin: 0;
  padding: 0;

  @media (min-width: $tablet-width) {
    min-height: 0;
    display: flex;
    flex-direction: row;
  }

  @media (min-width: $desktop-width) {
    min-height: 88px;
  }
}

.page-header__logo-link {
  display: flex;
  justify-content: center;
  padding-top: 22px;
  padding-bottom: 20px;

  @media (min-width: $tablet-width) {
    position: absolute;
    margin-top: 0;
    margin-bottom: 0;
    order: -1;
    z-index: 10;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    top: -17px;
    left: 450px;
    justify-content: center;
    margin-top: 0;
    margin-bottom: 0;
    order: 1;
    z-index: 10;
  }
}

.page-header__logo {
  width: 86px;
  height: 35px;

  @media (min-width: $tablet-width) {
    width: 112px;
    height: 46px;
  }

  @media (min-width: $desktop-width) {
    width: 150px;
    height: 80px;
  }
}

.page-header__logo-link:hover {
  opacity: 0.6;
}

.page-header__logo-link:active {
  opacity: 0.3;
}
