@import "../abstracts/variables.scss";
@import "../abstracts/mixins.scss";

.catalog-list {
  list-style: none;
  padding: 0;
  margin: 0;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}

.catalog-list__item {
  padding: 0;
  margin: 0;
  margin-top: 30px;
  border: 1px solid $Gallery;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;

  @media (min-width: $tablet-width) {
    margin-top: 52px;
    height: 334px;
    flex-direction: row;
  }

  @media (min-width: $desktop-width) {
    margin-top: 112px;
    min-height: 659px;
    min-width: 316px;
    flex-direction: column;
  }
}

.catalog-list__item:hover {
  box-shadow: 0 6px 10px $blackRgba;
}

.catalog-list__item:active {
  box-shadow: 0 3px 5px $blackRgba;
}

.catalog-list__image-container {
  @media (min-width: $tablet-width) {
    height: 334px;
  }

  @media (min-width: $desktop-width) {
    min-height: 462px;
  }
}

.catalog-list__text-container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding: 25px 5px 27px 27px;

  @media (min-width: $tablet-width) {
    min-height: 282px;
    padding: 29px 39px 26px 33px;
  }

  @media (min-width: $desktop-width) {
    min-height: 145px;
    padding: 26px 26px 26px 28px;
    width: 262px;
  }
}
